import * as THREE from "three";
import URDFLoader from "urdf-loader";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";

document.addEventListener("DOMContentLoaded", function () {
  const viewer = document.getElementById("viewer");
  if (!viewer) return;

  const scene = new THREE.Scene();
  scene.background = new THREE.Color(0xcee6f0);

  const camera = new THREE.PerspectiveCamera(
    75,
    viewer.clientWidth / viewer.clientHeight,
    0.1,
    1000
  );
  camera.position.set(2, 2, 2);
  camera.lookAt(0, 0, 0);

  const renderer = new THREE.WebGLRenderer({ antialias: true });
  renderer.shadowMap.enabled = true;
  renderer.shadowMap.type = THREE.PCFSoftShadowMap;
  renderer.setSize(viewer.clientWidth, viewer.clientHeight);
  viewer.appendChild(renderer.domElement);

  const directionalLight = new THREE.DirectionalLight(0xffffff, 1.0);
  directionalLight.castShadow = true;
  directionalLight.shadow.mapSize.setScalar(1024);
  directionalLight.position.set(5, 30, 5);
  scene.add(directionalLight);

  const ambientLight = new THREE.AmbientLight(0xffffff, 0.2);
  scene.add(ambientLight);

  const ground = new THREE.Mesh(
    new THREE.PlaneGeometry(100, 100),
    new THREE.ShadowMaterial({ opacity: 0.25 })
  );
  ground.rotation.x = -Math.PI / 2;
  ground.receiveShadow = true;
  scene.add(ground);

  const controls = new OrbitControls(camera, renderer.domElement);
  controls.minDistance = 4;
  controls.target.y = 1;
  controls.update();

  const manager = new THREE.LoadingManager();
  const loader = new URDFLoader(manager);
  let robot;

  loader.load("urdf/urdf.urdf", function (result) {
    robot = result;
    robot.rotation.x = Math.PI * -0.5;
    robot.traverse(function (c) {
      c.castShadow = true;
    });
    robot.updateMatrixWorld(true);
    const bb = new THREE.Box3().setFromObject(robot);
    robot.position.y -= bb.min.y;
    scene.add(robot);
  });

  manager.onLoad = () => {
    // Handle completed loading if needed
  };

  function animate() {
    requestAnimationFrame(animate);
    renderer.render(scene, camera);
  }
  animate();

  window.onunload = () => {
    scene.clear();
    renderer.dispose();
    viewer.removeChild(renderer.domElement);
  };
});
